.home{
    padding-bottom: 48px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
      margin-top: 70px;
    }
    .home-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }
    .picture{
        height: 420px;
    }
    .home-info{
        font-size: 24px;
    }
    .info-name{
        color: blue;
        font-size: 32px;
    }
    @media only screen and (max-width: 720px){
        .home-top{
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        .picture{
            height: 250px;
        }
        .home-info{
            font-size: 20px;
        }
        .info-name{
            font-size: 26px;
        }
    }